import { css } from "styled-components";

export const Frosted = css`
    border: none;
    outline: none;
    padding: 8px;
    border-radius: 8px;
    background-color: rgba(0, 0, 0, 0.2);
    box-shadow: 0 0 1em rgba(58, 57, 57, 0.2);
`;

export const FrostedOpaque = css`
    border: none;
    outline: none;
    padding: 8px;
    border-radius: 8px;
    background-color: black;
    box-shadow: 0 0 1em rgba(58, 57, 57, 0.2);
`;
